:root {
  --bg-color: hsl(0, 0%, 7%);
  --title-color: hsl(0, 84%, 64%);
  --description-color: hwb(0 100% 0%);
  --fade-text-color: rgb(36, 36, 36);
}

:root {
  --title-font: "bebas neue", serif;
  --description-font: "uchen", serif;
  --navmenu-font: "quicksand", serif;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: var(--title-font);
}

p {
  font-family: var(--description-font);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  background-color: var(--bg-color);
}

body,
ul {
  margin: 0;
  padding: 0;
  color: var(--white);
}

ul {
  list-style: none;
}

a {
  color: var(--white);
}

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* Navbar */
@keyframes hide-links {
  from {
    visibility: visible;
    opacity: 1;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
@media (min-width: 64em) {
  .hide-links {
    visibility: visible;
  }
}
@media (max-width: 63.9375em) {
  .hide-links {
    visibility: hidden;
    animation: hide-links 300ms ease-in-out forwards;
  }
}

@keyframes show-links {
  from {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
@media (min-width: 64em) {
  .show-links {
    visibility: visible;
    animation: show-links 0ms ease-in-out forwards;
  }
}
@media (max-width: 63.9375em) {
  .show-links {
    visibility: hidden;
    animation: show-links 300ms ease-in-out forwards;
  }
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
